* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

ul  {
  list-style: none;
  padding: 0;
}

img {
  display: block;
  object-fit: contain;
}

.title{
  font-weight: bold;
}

.title-content{
  font-weight: 400;
}

.country-list li {
  margin-bottom: 25px;
  display: flex;
}

.country-list p {
  margin-left: 20px;
  font-size: 16px;
}